.header {
  width: 100%;
  min-width: 320px;
  height: 230px;
  color: var(--text-white);
  background: #0000008f url("mobile_header_library@1x.53dfecf8.jpg") 0 0 / 100% 100% no-repeat;
  margin: 0;
  padding: 0;
  box-shadow: inset 0 0 70px 50px #00000f8f;
}

.header__main {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 3.375rem;
  display: flex;
}

.header__title {
  align-items: center;
  gap: .5rem;
  display: flex;
}

.header__title__name {
  font-size: 1.875rem;
  display: none;
}

.header__nav {
  font-size: var(--fs-300);
  text-transform: uppercase;
  gap: 2.5rem;
  font-weight: 500;
  display: flex;
}

.header__nav__active {
  position: relative;
}

.header__nav__active:after {
  content: "";
  width: 100%;
  height: 3px;
  background-color: var(--text-red-warn);
  position: absolute;
  top: 14px;
  left: 0;
}

.button--library {
  color: var(--bg-white);
  border: 1px solid var(--bg-white);
  text-transform: uppercase;
  width: 130px;
  cursor: pointer;
  background-color: #0000;
  border-radius: 5px;
  padding-top: 14px;
  padding-bottom: 12px;
  font-family: Roboto;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: 133%;
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
}

.button--library:active {
  background-color: var(--text-orange);
}

.button__buttons:hover, .button__btn:focus {
  background-color: var(--text-orange);
  border-radius: 10px;
  transform: scale(1.1);
  box-shadow: 0 4px 4px #00000026;
}

